import Swal from "sweetalert2";
export const SuccessNotification = () => {
  
    const Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    })
    
    Toast.fire({
      icon: 'success',
      title: 'laq uurla yaradld'
    })
  };
 export  const onFinishFailed = (errorInfo) => {
  Swal.fire({
    icon: 'error',
    title: 'Xta...',
    text: errorInfo,
   
  })
  };
  
    export const Remove=()=>{
      Swal.fire({
        title: 'Silmk istyirsiz?',
        text: "Yenidn geri qaytarmaq olmaz!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Razyam!'
      }).then((result) => {
        if (result.isConfirmed) {
          Swal.fire(
            'SLND!',
            'Mlumat artiq silidi.',
            'success'
          )
        }
      })
    }
    export const ShowContactDetails=(contactdata)=>{
      const message = "Ad: " + contactdata.name + ",<br>" +
      "Soyad: " + contactdata.surname + ",<br>" +
      "Ata ad: " + contactdata.father + ",<br>" +
      "Ixtisas: " + contactdata.profession;
      Swal.fire({
        icon: 'info',
        title: message,
        footer: '2023.05.18'
        
      })
      
    }
    
